// Configuracion general
@import "config";
// Tema. Los componentes del tema en front
@import "theme/back/header";
@import "theme/back/steps";
@import "theme/back/container";
@import "theme/back/asideMenu";
@import "theme/front/profile";
@import "theme/front/footer";

.MenuAdmin {
  padding: 0;
  margin: 0;
  li {
    flex-grow: 1;
    text-align: center;
    background: #002444;
    margin: 0 rem(4) rem(5); }
  a {
    padding: rem(10);
    display: block;
    color: white; } }

.messages-success {
  text-align: center;
  padding: rem(18) 0 rem(15);
  background: #98f6c0;
  margin: rem(24);
  display: none; }
.Admin-updateStateProvider, .Button-table {
  background: #002444;
  padding: rem(6) rem(12);
  color: white;
  &.image {
    padding: 0;
    color: #002444;
    background: white;
    @include respond-to(medium) {
      color: red; } } }


.Files-container {
  width: 100vw;
  height: 100vh;
  background: rgba(255, 255, 255, 0.7);
  top: 0;
  left: 0;
  position: fixed;
  display: none; }

.showFiles {
  display: flex; }

.Files {
  background: white;
  position: relative;
  padding: rem(20) rem(25);
  box-shadow: 0 0 rem(8) rgba(0, 0, 0, 0.1);
  h2 {
    color: #002444; }

  .close {
    position: absolute;
    top: -1rem;
    right: -1rem;
    background: #002444;
    font-size: rem(25);
    padding: rem(6) rem(12);
    color: white;
    cursor: pointer; } }

.hidden {
    display: none; }


#loader-wrapper {
    background-color: rgba(0, 0, 0, 0.76);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000; }

#loader {

    display: block;
    position: relative;
    left: 50%;
    top: 50%;
    width: 150px;
    height: 150px;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #3498db;
    animation: spin 2s linear infinite; }


#loader:before {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    right: 5px;
    bottom: 5px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #e74c3c;
    animation: spin 3s linear infinite; }


#loader:after {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    right: 15px;
    bottom: 15px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: #f9c922;
    animation: spin 1.5s linear infinite; }


@keyframes spin {
    0% {
        transform: rotate(0deg); }
    100% {
        transform: rotate(360deg); } }
